@use '../base/variables' as v;
@use '../base/mixins' as m;

.contenido_web { 
    display: flex;

    background: v.$purple0;


    // @include m.telefono{
    //     flex-direction: column; 
    // }
  

    @include m.telefono {
        flex-direction: column;

    }

    @include m.tablet{
        flex-direction: row;
        //? Invertir índice
        //flex-direction: row-reverse;
    }
}

.italica {
    font-style: italic;
    text-wrap: wrap;
}

#enlace {
    color: v.$bgbar;
    cursor: pointer;
    text-wrap: wrap !important;
}

.fecha { 
    display: block;
    font-weight: 500;
    text-wrap: wrap;

}
#referencia{
    text-wrap: wrap;
    display: inline-block;
    margin: 1rem 0rem;
}