@use '../base/variables' as v;
@use '../base/mixins' as m;


// layout del índice
.indice {
    overflow-y: scroll;
    scrollbar-color: v.$purple9 v.$purple2;
    scrollbar-width: thin;

    box-shadow: 2px 0px 4px;
    background: v.$bgindice;
    z-index: 800;
   
    @include m.telefono{
        display: none;
    }

    @include m.tablet{
        display: inline;
        transition: all 0.5s;
        // display: flex;
        top: -1px;
        position: sticky;
        flex-direction: column;
        justify-content: center;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        max-height: 100vh;
        padding: 8rem 2rem 1rem 2rem;
        flex-grow: -10;
        width: 40%;
        min-width: 287px;
        flex-wrap: wrap;
        margin: 0;
        &-cabecera{
           text-align: center;
        }
    }
 
    .indice-cabecera {
        font-family: v.$fuente_indice_titulo;
        font-family: v.$fuente_parrafos;

        .titulo-indice {
            font-weight: 500;
        }
        
    }

}



.indice-lista {
    font-size: v.$T_fuente_indice;
    font-family: v.$fuente_indice_lista;
    font-weight: 600;
    @include m.telefono{
        align-items: center;
        flex-direction: column;
    }
 
}

// Marcadores
.nav-line,.nav-circle {
    display: inline-block;
    background-color: #000;
    margin-right: 1rem;
}


// Contenedor de links
.indice-lista__elemento {

    display: flex;
    align-items: center;
    padding: 0rem 2rem;
    margin: 0rem 4rem;
    margin: 2rem auto auto auto;
    min-height: 2rem;
    color: v.$purple12;
    font-weight: 700;

    @include m.tablet {
        .nav-line{
            height: 2px;
            min-width: 1.5rem;
            transition: all v.$anim_duration;
            background-color: v.$purple11;
        }
    
        &:hover > .nav-line{
            min-width: 3.5rem;
            margin-right: 1rem;
            background-color: v.$purple12;
            font-weight: bold;
            transition-duration: v.$anim_duration;
        }
    
        &:hover > .indice-text{
            color: v.$purple12;
            transition-duration: v.$anim_duration;
        }
    
        &:hover > .nav-circle{
            min-width: 2.5rem;
            background-color: v.$purple12;
            font-weight: bold;
            border-radius: 30%;
            transition-duration: v.$anim_duration;
        }
    color: v.$purple11;

    }

}

@include m.tablet {
    .active {

        .nav-line{
            min-width: 3rem;
            margin-right: 1rem;
            background-color: v.$purple12;
            font-weight: bold;
            transition-duration: v.$anim_duration;
        }
    
        .indice-text{
            color: v.$purple12;
            transition-duration: v.$anim_duration;
        }
    
        .nav-circle{
            min-width: 2rem!important;
            background-color: v.$purple12 !important;
            font-weight: bold;
            border-radius: 30%!important;
            transition-duration: v.$anim_duration;
        }
    }    

}

// Sublista
.sublista-indice{
    .indice-lista__elemento {
        margin-left: 4rem;
        margin-top: 1rem;
    }

    .nav-circle {
        height: 0.5rem;
        min-width: 0.5rem;
        margin-right: 1rem;
        border-radius: 50%;
        background-color: v.$purple11;
        transition: all v.$anim_duration;
    }

}

@include m.telefono {
    .hide-phone {
        transition: transform 0.5s;
        transform: translateY(-100rem);
    }
}

@include m.tablet {
    .hide {
        transition: all 0.5s;
        box-shadow: none;
        background: v.$sidebar;
        transform: translateX(-35rem);
        overflow-y: hidden;
    }
}
