@use '../base/variables' as v;
@use '../base/mixins' as m;

.blog {

    @include m.telefono {
        hr{
            display: block;
        }
        padding: 3rem 5rem;
        h1 {
            font-size: 4.9rem;
        }
    }

   @include m.tablet {
        hr{
            display: none;
        }
        box-shadow: inset 0px 0px 8px;
        backdrop-filter: blur(16px);
        -webkit-backdrop-filter: blur(16px);
        background-color: rgba(255, 255, 255, 0.1);
        border-radius: 12px;
        border: 1px solid rgba(31, 0, 34, 0);

        transform: translateY(0rem);
        transition: transform 0.5s;
        width: 100%;
        padding: 5.7rem 2.4vw;
        &-text{
            font-family: v.$fuente_parrafos;
            font-size: v.$T_fuente_parrafos;
            font-weight: 400;
            color: #101;
        }

    }

}

.nota{
    background-color: v.$nota;
    backdrop-filter: blur(16px);
    border-radius: 1rem;
    padding: 2rem;
    margin-top: 2rem;
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(204, 153, 238, 0.75);
    border-radius: 12px;
    border: 1px solid rgba(209, 213, 219, 0.3);
    box-shadow: 0px 0px 2px;

    &-header{
        display: flex;
        gap: 0.5rem;
    }
}


.entrada {
    margin: 3rem 0 5rem 0;
    &-encabezado {
        margin: 0;
        h1 ,h2,h3 {
            font-family: v.$fuente_indice_lista;
            margin: 0 0 2.5rem 0;
            font-weight: 600;
        }
        h1 {
            color: v.$pink3;
        }
        h2 {
            color: v.$pink2;
        }
        h3 {
            color: v.$pink1;
            font-weight: 500;
        }

    }
    
}

@include m.tablet {
    .move {
        transition: all 0.5s ;
        width: 100%;
        transform: translateX(-10vw);
    }
}


#lista {
    list-style: disc;
    margin-left: 3.5rem;
    line-height: 3rem;
}

.bold {
    font-weight: 600;
}

.instituciones {
    margin-bottom: 1.5rem;
}

.line {
    display: inline-block;
    margin-bottom: 0.5rem;
    height: 3px;
    min-width: 1.5rem;
    background-color: v.$purple11;
}

