@use '../base/mixins' as m;
@use '../base/variables' as v;




.icon {

    // Claro
    transition: all 0.2s !important;
    text-align: center;

    background-color: v.$button;
    border-radius: 12px;
    box-shadow:none;
    padding: 0.2rem;
    z-index: 1000;
    transition: transform 0.2s;
    box-sizing: content-box;    

    @include m.tablet {
        display: inline-block !important;
        z-index: 10000;
        position: fixed !important;
        box-shadow: 0px 0px 5px #000, 0px 0px 5px #000;
        margin: 1.2rem;
        padding: auto !important;
        border-radius: 20%;
        transform: rotate(-90deg);
        transition: transform 0.2s;
        cursor: pointer;
        width: fit-content !important;
        text-align: left;
    }

    @include m.telefono {
        display: none;

    }


    &:hover {
        transition: background 10ms;
        background-color: v.$buttonhov !important;
    }

}

.bar {
    @include m.tablet{
        display: inline-block !important;
        z-index: 100;
        position: fixed;
        top: 0;
        background-color: v.$sidebar;
        height: 100vh;
        width: 7rem;
    }

    @include m.telefono{
        display: none;

    }
}


@include m.tablet{
    .rotate {
    transition: transform 0.2s;
    transform: rotate(0.1deg);
    }
}

.side-bar {
    z-index: inherit;
}